header {
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  position: fixed;
  left: 0;
  z-index: 12;
  top: 0;
  width: 100%;
  .navHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 20px;
    #navMenu{
      .displayMenu{
        display: none;
      }
    }
    .logo {
      width: 50px;
    }
    .navButton {
      display: none;
      @include mediaQuery("medium") {
        display: block;
      }
      img {
        text-align: right;
        width: 25px;
        padding: 17px 0;
        @include mediaQuery("small") {
          margin-right: 41px;
        }
      }
    }
    ul {
      margin: 0 auto 0 35% !important;
      @include mediaQuery("large") {
        margin: 0 40% 0 14%;
      }
      li {
        list-style: none;
        margin-right: 20px;
        a {
          text-decoration: none;
          @include textStyle($fs-0, $fw-0, $color-black);
        }
      }
    }
    .navRight {
      display: flex;
      align-items: center !important;
      .logoutButton {
        box-shadow: none;
      }
      .avatar {
        @include textStyle($fs-0, $fw-0, black);
        position: relative;
        text-decoration: none;
        cursor: pointer;
        margin-right: 15px;
        img {
          height: auto;
          width: 30px;
          border-radius: 25px;
          margin-right: 5px;
        }
      }
      p {
        font-size: 15px;
        margin-bottom: 0;
      }
      a{
        text-decoration: none;
      }

      .selectLocation {
        position: relative;
        align-items: center;
        // margin-left: 10px;
        .locationCity {
          @include textStyle($fs-0 - 1, $fw-0, $color-gray-1);
        }
        i:nth-child(1) {
          padding: 0 10px;
          color: $color-gray-1;
        }
        .dropdown-toggle::after {
          margin-left: 1.855em;
          border-top: 0.5em solid;
          border-right: 0.5em solid transparent;
          border-bottom: 0;
          border-left: 0.5em solid transparent;
        }
        .dropdown-menu {
          background: #fff !important;
          top: 200% !important;
          position: absolute;
          top: 100%;
          left: 0;
          z-index: 1000;
          float: left;
          min-width: 160px;
          padding: 5px 0;
          margin: 2px 0 0;
          font-size: 14px;
          text-align: left;
          list-style: none;
          background-color: #fff;
          -webkit-background-clip: padding-box;
          background-clip: padding-box;
          border: 1px solid #ccc;
          border: 1px solid rgba(0, 0, 0, 0.15);
          border-radius: 4px;
          -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
          box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
          li {
            margin-right: 0;
            a {
              display: block;
              padding: 3px 20px;
              clear: both;
              font-weight: 400;
              line-height: 1.42857143;
              color: #333;
              white-space: nowrap;
              transition: all 0.3s;
              &:hover {
                background-color: #60c5ef;
                color: #fff;
              }
            }
          }
        }
        .selectScroll {
          max-height: 300px;
          overflow-y: scroll;
          border-radius: 7px;
        }
        .selectMenu {
          @include textStyle($fs-0 - 1, $fw-0, $color-gray-1);
        }
      }
    }
    #idnavMobile {
      display: none !important;
      // @include mediaQuery('medium'){
      //     display: block;
      // }
    }
  }
}
